.card {
    width: 330px;
    height: 430px;
    border-radius: 8px;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2);
    overflow: hidden;
    margin: 20px;
    text-align: center;
    transition: all 0.25s;
}

.card:hover{
    transform: translateY(-15px);
    box-shadow: 0, 12px, 16px rgba(0, 0, 0, 0.2);
}

.card img{
    width: 330px;
    height: 220px;
}

.card h4{
    font-family: Helvetica, Arial, sans-serif;
    font-weight: 600;
}